.dialogs {
    display: grid;
    grid-template-areas: "i m" ;
    grid-template-columns: 250px auto;
    font-size: 16px;
}

.dialogsItems{
    grid-area: i;
}
.messages {
    grid-area: m;
}

.message {
    font-size: 18px;
    margin: 10px 0 10px 0;
}

.area {
    resize: none;
}
